.App__header {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 45px 200px 40px;
    padding-bottom: 37px;
    border-bottom: solid 1px rgba(84, 84, 84, 0.7);
    position: relative;
}

@media screen and (max-width: 1070px) {
    .App__header {
        margin: 40px 100px 40px;
    }
}

@media screen and (max-width: 870px) {
    .App__header {
        margin: 35px 50px 40px;
     }
}

@media screen and (max-width: 530px) {
    .App__header {
        margin: 28px 0 40px;
        padding: 0 30px 28px;
    }
}